import React, { useState, useEffect } from 'react';
import { useUser } from '../context/UserContext';
import { useNavigate, Link } from 'react-router-dom';
import { Atom, Beaker, FlaskConical, Eye, EyeOff } from 'lucide-react';

const Login = () => {
  const [form, setForm] = useState({ email: '', password: '' });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [debug, setDebug] = useState('');
  const { login } = useUser();
  const navigate = useNavigate();

  // Debug logging
  useEffect(() => {
    console.log('üîç Login component mounted');
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    console.log(`üîç Field ${name} changed:`, value);
    setForm(prev => ({ ...prev, [name]: value }));
    setError('');
    setDebug('');
  };

  const validateForm = () => {
    console.log('üîç Validating form:', form);

    if (!form.email || !form.password) {
      setError('Email and password are required');
      return false;
    }

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(form.email)) {
      setError('Invalid email format');
      return false;
    }

    console.log('üîç Form validation passed');
    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log('üîç Login form submitted');

    if (!validateForm()) {
      return;
    }

    setLoading(true);
    setError('');
    setDebug('Attempting login...');

    try {
      console.log('üîç Sending login request to:', 'http://localhost:3001/api/auth/login');
      console.log('üîç Request data:', { email: form.email, password: '***' });

      const res = await fetch('http://localhost:3001/api/auth/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify(form),
      });

      console.log('üîç Response status:', res.status);
      console.log('üîç Response headers:', Object.fromEntries(res.headers.entries()));

      const data = await res.json();
      console.log('üîç Response data:', data);

      if (res.ok) {
        console.log('üîç Login successful, storing token and setting user data');

        // Store JWT token in localStorage
        localStorage.setItem('authToken', data.token);

        login(data.user);
        setDebug('Login successful! Redirecting...');
        setTimeout(() => {
          navigate('/app');
        }, 1000);
      } else {
        console.log('üîç Login failed:', data.error);
        setError(data.error || 'Invalid email or password');
        setDebug('');
      }
    } catch (error) {
      console.error('üîç Login error:', error);
      setError('Connection failed. Please try again.');
      setDebug(`Network error: ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-md w-full space-y-8">
        {/* Header */}
        <div className="text-center">
          <div className="flex justify-center mb-6">
            <div className="relative">
              <div className="w-20 h-20 border-4 border-cyan-400 rounded-full flex items-center justify-center animate-pulse">
                <Atom className="w-10 h-10 text-cyan-400" />
              </div>
              <div className="absolute -top-2 -right-2 w-6 h-6 bg-cyan-500 rounded-full flex items-center justify-center">
                <Beaker className="w-3 h-3 text-white" />
              </div>
            </div>
          </div>
          <h2 className="text-4xl font-bold bg-gradient-to-r from-cyan-400 to-blue-400 bg-clip-text text-transparent mb-2">
            Welcome to QChem Axis
          </h2>
          <p className="text-cyan-300 text-lg">
            Sign in to continue your chemistry journey
          </p>
        </div>

        {/* Form */}
        <div className="bg-slate-800/50 backdrop-blur-lg rounded-2xl border border-cyan-400/30 p-8 shadow-2xl">
          <form className="space-y-6" onSubmit={handleSubmit}>
            <div className="space-y-4">
              <div>
                <label htmlFor="email" className="block text-sm font-medium text-cyan-300 mb-2">
                  Email Address
                </label>
                <input
                  id="email"
                  name="email"
                  type="email"
                  required
                  className="w-full px-4 py-3 bg-slate-700/50 border border-cyan-400/30 rounded-lg text-white placeholder-cyan-300/50 focus:outline-none focus:ring-2 focus:ring-cyan-400 focus:border-transparent transition-all"
                  placeholder="your@email.com"
                  value={form.email}
                  onChange={handleChange}
                />
              </div>

              <div>
                <label htmlFor="password" className="block text-sm font-medium text-cyan-300 mb-2">
                  Password
                </label>
                <div className="relative">
                  <input
                    id="password"
                    name="password"
                    type={showPassword ? "text" : "password"}
                    required
                    className="w-full px-4 py-3 bg-slate-700/50 border border-cyan-400/30 rounded-lg text-white placeholder-cyan-300/50 focus:outline-none focus:ring-2 focus:ring-cyan-400 focus:border-transparent transition-all pr-12"
                    placeholder="Enter your password"
                    value={form.password}
                    onChange={handleChange}
                  />
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    className="absolute right-3 top-1/2 transform -translate-y-1/2 text-cyan-400 hover:text-cyan-300"
                  >
                    {showPassword ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                  </button>
                </div>
              </div>
            </div>

            {/* Error Display */}
            {error && (
              <div className="p-4 bg-red-500/20 border border-red-400/50 rounded-lg">
                <p className="text-sm text-red-300">{error}</p>
              </div>
            )}

            {/* Debug Display */}
            {debug && (
              <div className="p-4 bg-cyan-500/20 border border-cyan-400/50 rounded-lg">
                <p className="text-sm text-cyan-300">{debug}</p>
              </div>
            )}

            <button
              type="submit"
              disabled={loading}
              className="w-full py-3 px-4 bg-gradient-to-r from-cyan-500 to-blue-500 text-white font-semibold rounded-lg shadow-lg hover:from-cyan-600 hover:to-blue-600 focus:outline-none focus:ring-2 focus:ring-cyan-400 focus:ring-offset-2 focus:ring-offset-slate-900 disabled:opacity-50 disabled:cursor-not-allowed transition-all transform hover:scale-105"
            >
              {loading ? (
                <span className="flex items-center justify-center">
                  <FlaskConical className="w-5 h-5 mr-2 animate-spin" />
                  Signing in...
                </span>
              ) : (
                'Sign In'
              )}
            </button>
          </form>
        </div>

        {/* Sign Up Link */}
        <div className="text-center">
          <p className="text-cyan-300">
            Don't have an account?{' '}
            <Link
              to="/signup"
              className="font-medium text-cyan-400 hover:text-cyan-300 transition-colors underline"
            >
              Sign up here
            </Link>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Login;
