import React, { useState, useEffect } from 'react';
import { useUser } from '../context/UserContext';
import { useNavigate } from 'react-router-dom';

const Preferences = () => {
  const [preferences, setPreferences] = useState({
    learning_style: '',
    interests: [],
    preferred_method: ''
  });
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);
  const { user, updateUser } = useUser();
  const navigate = useNavigate();

  const learningStyles = ['visual', 'auditory', 'kinesthetic'];
  const explanationPreferences = ['short', 'detailed'];
  const availableInterests = [
    'Organic Chemistry',
    'Inorganic Chemistry',
    'Physical Chemistry',
    'Analytical Chemistry',
    'Biochemistry',
    'Quantum Chemistry',
    'Environmental Chemistry',
    'Materials Chemistry'
  ];

  useEffect(() => {
    fetchPreferences();
  }, []);

  const fetchPreferences = async () => {
    try {
      const res = await fetch('http://localhost:3001/api/preferences/user', {
        credentials: 'include'
      });
      if (res.ok) {
        const data = await res.json();
        setPreferences({
          learning_style: data.learning_style || '',
          interests: data.interests || [],
          preferred_method: data.preferred_method || ''
        });
      }
    } catch (error) {
      console.error('Failed to fetch preferences:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleInterestToggle = (interest) => {
    setPreferences(prev => ({
      ...prev,
      interests: prev.interests.includes(interest)
        ? prev.interests.filter(i => i !== interest)
        : [...prev.interests, interest]
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSaving(true);
    
    try {
      const res = await fetch('http://localhost:3001/api/preferences/save', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',
        body: JSON.stringify(preferences),
      });
      const data = await res.json();
      if (res.ok) {
        // Update user data with preferences and mark onboarding as completed
        updateUser({ 
          learning_style: preferences.learning_style,
          interests: preferences.interests,
          preferred_method: preferences.preferred_method,
          onboarding_completed: 1
        });
        alert('Preferences saved successfully!');
        navigate('/app');
      } else {
        alert(data.error);
      }
    } catch (error) {
      alert('Failed to save preferences');
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-lg">Loading preferences...</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-2xl mx-auto">
        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            Customize Your Learning Experience
          </h1>
          <p className="text-gray-600">
            Tell us about your preferences to personalize your chemistry learning journey
          </p>
        </div>

        <form onSubmit={handleSubmit} className="space-y-8">
          {/* Learning Style */}
          <div className="bg-white shadow rounded-lg p-6">
            <h2 className="text-lg font-semibold mb-4">Learning Style</h2>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              {learningStyles.map(style => (
                <label
                  key={style}
                  className={`flex items-center p-4 border rounded-lg cursor-pointer transition-all ${
                    preferences.learning_style === style
                      ? 'border-indigo-500 bg-indigo-50'
                      : 'border-gray-300 hover:border-gray-400'
                  }`}
                >
                  <input
                    type="radio"
                    name="learning_style"
                    value={style}
                    checked={preferences.learning_style === style}
                    onChange={(e) => setPreferences({ ...preferences, learning_style: e.target.value })}
                    className="mr-3"
                  />
                  <div>
                    <div className="font-medium capitalize">{style}</div>
                    <div className="text-sm text-gray-500">
                      {style === 'visual' && 'Learn through diagrams and charts'}
                      {style === 'auditory' && 'Learn through explanations and discussions'}
                      {style === 'kinesthetic' && 'Learn through hands-on activities'}
                    </div>
                  </div>
                </label>
              ))}
            </div>
          </div>

          {/* Interests */}
          <div className="bg-white shadow rounded-lg p-6">
            <h2 className="text-lg font-semibold mb-4">Areas of Interest</h2>
            <p className="text-sm text-gray-600 mb-4">Select all that interest you:</p>
            <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
              {availableInterests.map(interest => (
                <label
                  key={interest}
                  className={`flex items-center p-3 border rounded-lg cursor-pointer transition-all ${
                    preferences.interests.includes(interest)
                      ? 'border-indigo-500 bg-indigo-50'
                      : 'border-gray-300 hover:border-gray-400'
                  }`}
                >
                  <input
                    type="checkbox"
                    checked={preferences.interests.includes(interest)}
                    onChange={() => handleInterestToggle(interest)}
                    className="mr-2"
                  />
                  <span className="text-sm">{interest}</span>
                </label>
              ))}
            </div>
          </div>

          {/* Explanation Preference */}
          <div className="bg-white shadow rounded-lg p-6">
            <h2 className="text-lg font-semibold mb-4">Explanation Style</h2>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {explanationPreferences.map(pref => (
                <label
                  key={pref}
                  className={`flex items-center p-4 border rounded-lg cursor-pointer transition-all ${
                    preferences.preferred_method === pref
                      ? 'border-indigo-500 bg-indigo-50'
                      : 'border-gray-300 hover:border-gray-400'
                  }`}
                >
                  <input
                    type="radio"
                    name="preferred_method"
                    value={pref}
                    checked={preferences.preferred_method === pref}
                    onChange={(e) => setPreferences({ ...preferences, preferred_method: e.target.value })}
                    className="mr-3"
                  />
                  <div>
                    <div className="font-medium capitalize">{pref}</div>
                    <div className="text-sm text-gray-500">
                      {pref === 'short' && 'Concise explanations with key points'}
                      {pref === 'detailed' && 'Comprehensive explanations with examples'}
                    </div>
                  </div>
                </label>
              ))}
            </div>
          </div>

          <div className="flex justify-end">
            <button
              type="submit"
              disabled={saving || !preferences.learning_style || !preferences.preferred_method}
              className="px-6 py-3 bg-indigo-600 text-white rounded-md hover:bg-indigo-700 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {saving ? 'Saving...' : 'Continue to App'}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Preferences;
