export default function QChemAxis() {
  const { user } = useUser();
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [conversationHistory, setConversationHistory] = useState([]);
  const [debugInfo, setDebugInfo] = useState({ apiUrl: '', raw: null, error: null });
  const [showDebug, setShowDebug] = useState(false);
  const [activeTab, setActiveTab] = useState('chat');
  const messagesEndRef = useRef(null);
  const [periodicSearch, setPeriodicSearch] = useState('');
  const [coursesSearch, setCoursesSearch] = useState('');
  const [referencesSearch, setReferencesSearch] = useState('');
  const [branchesSearch, setBranchesSearch] = useState('');
  const [simulationsSearch, setSimulationsSearch] = useState('');

  const filteredElements = periodicElements.filter(el =>
    el.name.toLowerCase().includes(periodicSearch.toLowerCase()) ||
    el.s.toLowerCase().includes(periodicSearch.toLowerCase())
  );

  const filteredCourses = courses.map(section => ({
    ...section,
    items: section.items.filter(item =>
      item.name.toLowerCase().includes(coursesSearch.toLowerCase())
    )
  }));

  const filteredReferences = references.map(section => ({
    ...section,
    items: section.items.filter(item =>
      item.name.toLowerCase().includes(referencesSearch.toLowerCase())
    )
  }));

  const filteredBranches = branches.filter(branch =>
    branch.name.toLowerCase().includes(branchesSearch.toLowerCase())
  );

  const filteredSimulations = simulations.filter(sim =>
    sim.name.toLowerCase().includes(simulationsSearch.toLowerCase()) ||
    sim.description.toLowerCase().includes(simulationsSearch.toLowerCase())
  );

  useEffect(() => {
    if (messages.length === 0) {
      setMessages([{
        role: 'assistant',
        content: '‚öõÔ∏è QCHEM AXIS\nPowered by Quantum Vision\n\n‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ\n\nüî¨ Advanced Chemistry Intelligence System\n\n‚ú® Features:\n‚Ä¢ AI-Powered Answers with Formulas\n‚Ä¢ Beautiful Explanatory Files (HTML/PDF)\n‚Ä¢ Canva Design Integration\n‚Ä¢ PhET Interactive Simulations\n‚Ä¢ Comprehensive References & Courses\n\n‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ\n\nAsk any chemistry question...'
      }]);
    }
  }, [messages.length]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSubmit = async () => {
    if (!input.trim() || isLoading) return;
    const userMsg = input.trim();
    setInput('');
    const newUserMsg = { role: 'user', content: userMsg };
    setMessages(p => [...p, newUserMsg]);
    setConversationHistory(p => [...p, newUserMsg]);
    setIsLoading(true);

    try {
      // Build messages array for messages API
      const messages = [
        { role: 'system', content: 'You are QCHEM AXIS, an advanced quantum-level chemistry intelligence system. Provide comprehensive, scientifically accurate answers with clear explanations, balanced equations, formulas, and real-world applications.' },
        ...conversationHistory,
        { role: 'user', content: userMsg }
      ];

      // lazy-import helper to keep file small if not used elsewhere
      const { sendMessage } = await import('./lib/mistralClient.js');
      // Update debug state with apiUrl placeholder until response arrives
      const interimApiUrl = (import.meta.env.VITE_API_URL || (import.meta.env.DEV ? 'http://localhost:3001/api/mistral' : 'https://api.mistral.ai/v1/chat/completions'));
      setDebugInfo({ apiUrl: interimApiUrl, raw: null, error: null });

      const result = await sendMessage(messages, { model: import.meta.env.VITE_MODEL });

      const msg = result && result.text ? result.text : 'Unable to process query. Please ensure your API key is configured correctly.';

      const newAsstMsg = { role: 'assistant', content: msg };
      setMessages(p => [...p, newAsstMsg]);
      setConversationHistory(p => [...p, newUserMsg, newAsstMsg]);

      // Update debug info with actual result
      setDebugInfo({ apiUrl: result?.apiUrl || interimApiUrl, raw: result?.raw || null, error: null });

    } catch (err) {
      const errorMsg = err.message || 'Connection error. Please try again.';
      setMessages(p => [...p, { role: 'assistant', content: errorMsg }]);
    } finally {
      setIsLoading(false);
    }
  };

  const tabs = ['chat', 'periodic', 'branches', 'simulations', 'references', 'courses'];

  return (
    <div className="flex flex-col h-screen bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900">
      <div className="bg-gradient-to-r from-slate-900 via-blue-950 to-slate-900 border-b-4 border-cyan-400 text-white p-6 shadow-2xl">
        <div className="max-w-7xl mx-auto">
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center gap-4">
              <div className="relative">
                <div className="w-16 h-16 border-4 border-cyan-400 rounded-full flex items-center justify-center animate-pulse">
                  <Atom className="w-8 h-8 text-cyan-400" />
                </div>
              </div>
              <div>
                <h1 className="text-3xl font-bold bg-gradient-to-r from-cyan-400 to-blue-400 bg-clip-text text-transparent">
                  QCHEM AXIS
                </h1>
                <p className="text-cyan-300 text-sm">
                  Advanced Chemistry Learning Platform
                </p>
              </div>
            </div>
            <div className="flex items-center gap-2">
              <button 
                onClick={() => { 
                  setConversationHistory([]); 
                  setMessages([{ role: 'assistant', content: 'System Reset Complete' }]); 
                }} 
                className="flex items-center gap-2 bg-cyan-500/20 hover:bg-cyan-500/30 border border-cyan-400 px-5 py-3 rounded-lg transition-all"
              >
                <Trash2 className="w-5 h-5" />RESET
              </button>

              <button
                onClick={() => setShowDebug(s => !s)}
                className="flex items-center gap-2 bg-slate-800/40 hover:bg-slate-800/50 border border-slate-700 px-4 py-3 rounded-lg transition-all text-sm"
              >
                DEBUG
              </button>
            </div>
          </div>
          
          <div className="flex gap-2 flex-wrap mt-4">
            {tabs.map(tab => (
              <button
                key={tab}
                onClick={() => setActiveTab(tab)}
                className={`px-5 py-2 rounded-lg transition-all font-semibold ${
                  activeTab === tab
                    ? 'bg-cyan-500 text-white border-2 border-cyan-400'
                    : 'bg-slate-800/50 text-slate-300 hover:bg-slate-800 border-2 border-slate-700'
                }`}
              >
                {tab === 'chat' && '‚öõÔ∏è CHAT'}
                {tab === 'periodic' && 'üî¨ ELEMENTS'}
                {tab === 'simulations' && 'üß™ SIMULATIONS'}
                {tab === 'references' && 'üìö REFERENCES'}
                {tab === 'courses' && 'üéì COURSES'}
                {tab === 'branches' && 'üåø BRANCHES'}
              </button>
            ))}
          </div>

          {activeTab === 'periodic' && (
            <div className="mt-4">
              <input
                type="text"
                value={periodicSearch}
                onChange={(e) => setPeriodicSearch(e.target.value)}
                placeholder="Search elements by name or symbol..."
                className="w-full max-w-md px-4 py-2 bg-slate-800 border-2 border-slate-600 rounded-lg focus:outline-none focus:border-cyan-400 text-white placeholder-slate-400"
              />
            </div>
          )}

          {activeTab === 'courses' && (
            <div className="mt-4">
              <input
                type="text"
                value={coursesSearch}
                onChange={(e) => setCoursesSearch(e.target.value)}
                placeholder="Search courses..."
                className="w-full max-w-md px-4 py-2 bg-slate-800 border-2 border-slate-600 rounded-lg focus:outline-none focus:border-cyan-400 text-white placeholder-slate-400"
              />
            </div>
          )}

          {activeTab === 'references' && (
            <div className="mt-4">
              <input
                type="text"
                value={referencesSearch}
                onChange={(e) => setReferencesSearch(e.target.value)}
                placeholder="Search references..."
                className="w-full max-w-md px-4 py-2 bg-slate-800 border-2 border-slate-600 rounded-lg focus:outline-none focus:border-cyan-400 text-white placeholder-slate-400"
              />
            </div>
          )}

          {activeTab === 'branches' && (
            <div className="mt-4">
              <input
                type="text"
                value={branchesSearch}
                onChange={(e) => setBranchesSearch(e.target.value)}
                placeholder="Search branches..."
                className="w-full max-w-md px-4 py-2 bg-slate-800 border-2 border-slate-600 rounded-lg focus:outline-none focus:border-cyan-400 text-white placeholder-slate-400"
              />
            </div>
          )}

          {activeTab === 'simulations' && (
            <div className="mt-4">
              <input
                type="text"
                value={simulationsSearch}
                onChange={(e) => setSimulationsSearch(e.target.value)}
                placeholder="Search simulations..."
                className="w-full max-w-md px-4 py-2 bg-slate-800 border-2 border-slate-600 rounded-lg focus:outline-none focus:border-cyan-400 text-white placeholder-slate-400"
              />
            </div>
          )}
        </div>
      </div>

      {showDebug && (
        <div className="fixed right-4 bottom-4 w-96 max-h-96 overflow-auto bg-slate-900 border-2 border-cyan-400 rounded-lg p-4 text-sm text-slate-200 shadow-2xl">
          <div className="flex items-center justify-between mb-2">
            <strong className="text-cyan-300">Debug</strong>
            <button onClick={() => setShowDebug(false)} className="text-slate-400 hover:text-white">Close</button>
          </div>
          <div className="mb-2">
            <div className="text-xs text-slate-400">API URL</div>
            <div className="break-words text-white">{debugInfo.apiUrl || '‚Äî'}</div>
          </div>
          <div className="mb-2">
            <div className="text-xs text-slate-400">Last Error</div>
            <div className="break-words text-red-400">{debugInfo.error ? String(debugInfo.error) : '‚Äî'}</div>
          </div>
          <div>
            <div className="text-xs text-slate-400">Last Raw Response</div>
            <pre className="text-xs text-slate-300 whitespace-pre-wrap">{debugInfo.raw ? JSON.stringify(debugInfo.raw, null, 2) : '‚Äî'}</pre>
          </div>
        </div>
      )}

      <div className="flex-1 overflow-hidden">
        {activeTab === 'chat' && (
          <div className="h-full flex flex-col">
            <div className="flex-1 overflow-y-auto p-6">
              <div className="max-w-5xl mx-auto space-y-4">
                {messages.map((msg, i) => (
                  <div key={i} className={`flex ${msg.role === 'user' ? 'justify-end' : 'justify-start'}`}>
                    <div className={`max-w-4xl rounded-2xl p-5 shadow-2xl border-2 ${
                      msg.role === 'user' 
                        ? 'bg-gradient-to-br from-blue-600 to-cyan-600 border-cyan-400 text-white'
                        : 'bg-slate-800 border-slate-600 text-cyan-100'
                    }`}>
                      {msg.role === 'assistant' && (
                        <div className="flex items-center gap-2 mb-3 text-cyan-400">
                          <Atom className="w-5 h-5" />
                          <span className="font-bold text-sm tracking-wider">QCHEM AXIS</span>
                        </div>
                      )}
                      <div className="whitespace-pre-wrap leading-relaxed">{msg.content}</div>
                    </div>
                  </div>
                ))}
                {isLoading && (
                  <div className="flex justify-start">
                    <div className="bg-slate-800 border-2 border-cyan-400 rounded-2xl p-5 shadow-2xl">
                      <div className="flex items-center gap-3 text-cyan-400">
                        <Loader2 className="w-6 h-6 animate-spin" />
                        <div>Processing...</div>
                      </div>
                    </div>
                  </div>
                )}
                <div ref={messagesEndRef} />
              </div>
            </div>
            <div className="bg-slate-900 border-t-2 border-cyan-400 p-6">
              <div className="max-w-5xl mx-auto flex gap-3">
                <input 
                  type="text" 
                  value={input} 
                  onChange={(e) => setInput(e.target.value)} 
                  onKeyPress={(e) => e.key === 'Enter' && handleSubmit()} 
                  placeholder="Enter chemistry query..." 
                  className="flex-1 px-6 py-4 bg-slate-800 border-2 border-slate-600 rounded-xl focus:outline-none focus:border-cyan-400 text-white placeholder-slate-400" 
                  disabled={isLoading}
                />
                <button 
                  onClick={handleSubmit} 
                  disabled={isLoading || !input.trim()} 
                  className="bg-gradient-to-r from-cyan-500 to-blue-500 text-white px-8 py-4 rounded-xl font-bold disabled:from-gray-600 disabled:to-gray-700 shadow-lg flex items-center gap-2"
                >
                  {isLoading ? <Loader2 className="w-6 h-6 animate-spin" /> : <Send className="w-6 h-6" />}
                </button>
              </div>
            </div>
          </div>
        )}

        {activeTab === 'simulations' && (
          <div className="h-full overflow-y-auto p-6">
            <div className="max-w-6xl mx-auto">
              <h2 className="text-3xl font-bold mb-6 text-cyan-400 text-center">üß™ INTERACTIVE CHEMISTRY SIMULATIONS</h2>
              <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
                {filteredSimulations.map((sim, i) => (
                  <a
                    key={i}
                    href={sim.url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="bg-slate-800 border-2 border-slate-600 hover:border-cyan-400 rounded-xl p-6 transition-all hover:scale-105 shadow-lg"
                  >
                    <div className="flex items-center gap-3 mb-3">
                      <Video className="w-8 h-8 text-cyan-400" />
                      <h3 className="text-xl font-bold text-white">{sim.name}</h3>
                    </div>
                    <p className="text-slate-300 text-sm mb-3">{sim.description}</p>
                    <div className="flex items-center gap-2 text-cyan-400 font-semibold">
                      <span>Launch</span>
                      <ExternalLink className="w-4 h-4" />
                    </div>
                  </a>
                ))}
              </div>
            </div>
          </div>
        )}

        {activeTab === 'references' && (
          <div className="h-full overflow-y-auto p-6">
            <div className="max-w-6xl mx-auto">
              <h2 className="text-3xl font-bold mb-6 text-cyan-400 text-center">üìö CHEMISTRY REFERENCES</h2>
              <div className="grid md:grid-cols-2 gap-6">
                {filteredReferences.map((section, i) => (
                  <div key={i} className="bg-slate-800 border-2 border-slate-600 rounded-xl p-6">
                    <h3 className="text-2xl font-bold text-cyan-400 mb-4">{section.category}</h3>
                    <ul className="space-y-3">
                      {section.items.map((item, j) => (
                        <li key={j}>
                          <a
                            href={item.link}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="text-blue-400 hover:text-cyan-400 transition-colors"
                          >
                            {item.name} <ExternalLink className="w-4 h-4 inline ml-2" />
                          </a>
                        </li>
                      ))}
                    </ul>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}

        {activeTab === 'courses' && (
          <div className="h-full overflow-y-auto p-6">
            <div className="max-w-6xl mx-auto">
              <h2 className="text-3xl font-bold mb-6 text-cyan-400 text-center">üéì CHEMISTRY COURSES</h2>
              <div className="grid md:grid-cols-2 gap-6">
                {filteredCourses.map((section, i) => (
                  <div key={i} className="bg-slate-800 border-2 border-slate-600 rounded-xl p-6">
                    <h3 className="text-2xl font-bold text-cyan-400 mb-4">{section.category}</h3>
                    <ul className="space-y-3">
                      {section.items.map((item, j) => (
                        <li key={j}>
                          <a
                            href={item.link}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="text-blue-400 hover:text-cyan-400 transition-colors"
                          >
                            {item.name} <ExternalLink className="w-4 h-4 inline ml-2" />
                          </a>
                        </li>
                      ))}
                    </ul>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}

        {activeTab === 'periodic' && (
          <div className="h-full overflow-y-auto p-6">
            <div className="max-w-6xl mx-auto">
              <h2 className="text-3xl font-bold mb-6 text-cyan-400 text-center">üî¨ PERIODIC TABLE</h2>
              <div className="grid grid-cols-6 md:grid-cols-10 lg:grid-cols-18 gap-2">
                {filteredElements.map((el, i) => (
                  <div
                    key={i}
                    className="bg-slate-800 border-2 border-slate-600 rounded p-2 text-center hover:border-cyan-400 transition-colors cursor-pointer"
                    title={`${el.name} (${el.m})`}
                    onClick={() => { setActiveTab('chat'); setInput(`Explain ${el.name}`); }}
                  >
                    <div className="text-xs font-bold text-cyan-400">{el.n}</div>
                    <div className="text-lg font-bold text-white">{el.s}</div>
                    <div className="text-xs text-slate-400">{el.m}</div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}

        {activeTab === 'branches' && (
          <div className="h-full overflow-y-auto p-6">
            <div className="max-w-6xl mx-auto">
              <h2 className="text-3xl font-bold mb-6 text-cyan-400 text-center">üåø CHEMISTRY BRANCHES</h2>
              <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
                {filteredBranches.map((branch, i) => (
                  <div
                    key={i}
                    className={`bg-slate-800 border-2 border-slate-600 rounded-xl p-6 ${branch.c} hover:border-cyan-400 transition-all hover:scale-105 shadow-lg cursor-pointer`}
                    onClick={() => { setActiveTab('chat'); setInput(`Tell me more about ${branch.name}`); }}
                  >
                    <div className="flex items-center gap-3 mb-3">
                      {React.createElement(branch.icon, { className: "w-8 h-8 text-cyan-400" })}
                      <h3 className="text-xl font-bold text-white">{branch.name}</h3>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
